/*-----------------------------------------------------------------------------------*/
/* VARIABLES */
/*-----------------------------------------------------------------------------------*/

$form-margin-top-lg:             $paragraph-margin-top-lg;
$form-margin-bottom-lg:          $paragraph-margin-bottom-lg;
$form-margin-top-sm:             $paragraph-margin-top-sm;
$form-margin-bottom-sm:          $paragraph-margin-bottom-sm;

$input-cell-margin-bottom-lg:    1.5rem;
$input-cell-margin-bottom-sm:    1.5rem;

$label-font-lg:                  $body-size-lg;
$label-font-sm:                  $body-size-sm;
$label-line-height:              1.2;
$label-font-weight:              $weight-regular;

$label-margin-bottom-lg:         1.5rem;
$label-margin-bottom-sm:         0.8rem;

$input-font-lg:                  $body-size-lg;
$input-font-sm:                  $body-size-sm;
$input-line-height:              1.4;
$input-font-weight:              $weight-regular;

$input-border-width:             $button-border-width;
$input-border-style:             $button-border-style;
$input-border-radius:            $button-border-radius;
$input-border-color:             $color-grey-400;
$input-border-color-focus:       $color-grey-500;

$input-color:                    $body-color;
$input-placeholder-color:        $color-grey-500;

$input-background:               $color-white;

$input-padding-top-lg:           1.3rem;
$input-padding-right-lg:         1.7rem;
$input-padding-bottom-lg:        $input-padding-top-lg;
$input-padding-left-lg:          $input-padding-right-lg;

$input-padding-top-sm:           $button-padding-top-sm;
$input-padding-right-sm:         $button-padding-right-sm;
$input-padding-bottom-sm:        $button-padding-bottom-sm;
$input-padding-left-sm:          $button-padding-left-sm;

$input-small-size:               1.2rem;

$checkboxradio-width-lg:         2.5rem;
$checkboxradio-height-lg:        2.5rem;
$checkboxradio-width-sm:         2.5rem;
$checkboxradio-height-sm:        2.5rem;


/*-----------------------------------------------------------------------------------*/
/* STYLING */
/*-----------------------------------------------------------------------------------*/

form {
  margin-top: $form-margin-top-sm;
  margin-bottom: $form-margin-bottom-sm;

  @include media-up(md) {
    margin-top: $form-margin-top-lg;
    margin-bottom: $form-margin-bottom-lg;
  }

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }

}


/* --------- TEMPLATES --------- */

.form {

  &__row {
    @include cell-row;
    // @include flex-content-x(center);
  }

  &__col {
    @include cell;

    &--full {
      @include cell-span(12);
    }
    
    &--half {
      @include cell-span(6);
    }

  }

  &__footer {
    @include cell;
    @include cell-span(12);
    padding-top: 4rem;

    @include media-up(md) {
      padding-top: 4rem;
    }

  }

  &__note {
    padding-top: 3rem;
    padding-bottom: 3rem;

    @include media-up(md) {
      padding-top: 3rem;
      padding-bottom: 3rem;
    }

  }

  &__help {
    display: block;
    font-size: $input-small-size;
    color: $color-grey-600;
    margin-top: 1rem;
  }

  &__input {
    position: relative;
    margin-bottom: $input-cell-margin-bottom-sm;
    
    @include media-up(md) {
      margin-bottom: $input-cell-margin-bottom-lg;
    }
  
    &:first-child {
      margin-top: 0;
    }
  
    &:last-child {
      margin-bottom: 0;
    }

  }
  
}


/* --------- LABLES --------- */

label, .label {
  display: inline-block;
  font-size: $label-font-sm;
  font-weight: $label-font-weight;
  line-height: $label-line-height;
  margin-bottom: $label-margin-bottom-sm;

  @include media-up(md) {
    font-size: $label-font-lg;
    margin-bottom: $label-margin-bottom-lg;
  }

}


/* --------- INPUTS --------- */

input[type=text],
input[type=number],
input[type=email],
input[type=phone],
textarea {
  display: block;
  width: 100%;
  padding: $input-padding-top-sm $input-padding-right-sm $input-padding-bottom-sm $input-padding-left-sm;
  font-size: $input-font-sm;
  font-weight: $input-font-weight;
  line-height: $input-line-height;
  color: $input-color;
  background-color: $input-background;
  background-clip: padding-box;
  border: $input-border-width $input-border-style $input-border-color;
  border-radius: $input-border-radius;
  transition: border 0.3s ease-in-out, background-color 0.3s ease-in-out;

  @include media-up(md) {
    font-size: $input-font-lg;
    padding: $input-padding-top-lg $input-padding-right-lg $input-padding-bottom-lg $input-padding-left-lg;
  }

  @include placeholder {
    color: $input-placeholder-color;
  }

  &:focus {
    border-color: $input-border-color-focus;
    box-shadow: 0 2px 3px rgba($color-grey-600, 0.2);
  }

  &:disabled, &[readonly] {
    background-color: $color-grey-300;
    color: $input-placeholder-color;
    opacity: 1;
    pointer-events: none;

    // &:focus {
    //   border-color: $input-border-color;
    //   box-shadow: 0 0 0 rgba($color-grey-600, 0);
    // }

  }

}

textarea {
  min-height: 27rem;
  height: auto;
  overflow: auto;
  resize: vertical;
}


/* --------- CHECKBOX & RADIO --------- */

input[type=checkbox],
input[type=radio] {
  position: relative;
  cursor: pointer;
  overflow: hidden;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  height: $checkboxradio-height-sm;
  width: $checkboxradio-width-sm;
  display: inline-block;
  vertical-align: text-bottom;
  margin-right: 1rem;
  padding: 0;
  border: 0;
  border-radius: 0;

  @include media-up(md) {
    height: $checkboxradio-height-lg;
    width: $checkboxradio-width-lg;
  }

}

input[type=checkbox]::after,
input[type=radio]::after {
  position: relative;
  content: "";
  display: block;
  background-color: $input-background;
  background-clip: padding-box;
  border: $input-border-width $input-border-style $input-border-color;
  border-radius: $input-border-radius;
  height: $checkboxradio-height-sm;
  width: $checkboxradio-width-sm;

  @include media-up(md) {
    height: $checkboxradio-height-lg;
    width: $checkboxradio-width-lg;
  }

}

input[type=checkbox]::before,
input[type=radio]::before {
  position: absolute;
  top: 0;
  font-family: $font-fontawesome;
  font-weight: 900;
  content: "\f00c";
  display: block;
  font-size: 1.4rem;
  text-indent: 0.6rem;
  line-height: 2.6rem;
  text-align: left;
  height: $checkboxradio-height-sm;
  width: $checkboxradio-width-sm;
  color: transparent;
  z-index: 1;

  @include media-up(md) {
    height: $checkboxradio-height-lg;
    width: $checkboxradio-width-lg;
  }

}

input[type=checkbox]:focus, 
input[type=radio]:focus {
  outline: 0;
  outline-offset: 0;
}

input[type=checkbox]:checked::before,
input[type=radio]:checked::before {
  color: $color-black;
}


/* --------- REMOVE BROWSER INPUT STYLING --------- */

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}


/* --------- GROUP --------- */

.form {

  &__group {
    position: relative;
    @include d-flex;
    @include flex-wrap(wrap);

    input[type=text],
    input[type=number],
    input[type=email],
    input[type=phone],
    textarea {
      position: relative;
      @include flex (1 1 auto);
      width: 1%;
      min-width: 0;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }

    button {
      margin: 0 0 0 -0.2rem;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
      z-index: 1;
    }

  }

}


/* --------- VALIDATION --------- */

input[type=text],
input[type=number],
input[type=email],
input[type=phone],
textarea,
select {

  &.error {
    border-color: $color-danger;
  }

}

.error-message {
  display: block;
  font-size: $input-small-size;
  color: $color-danger;
  margin-top: 1rem;

  &::before {
    display: inline-block;
    font-family: $font-fontawesome;
    font-weight: 900;
    content: "\f071";
    margin-right: 0.8rem;
  }

}